@import "../../style/colors.scss";

#link {
    font-size: 18px;
    font-weight: bold;
    color: $title;
    padding: 0px;
    margin: 20px;
    text-decoration: none;
    text-transform: uppercase;
    z-index: 0;
    position: relative;
    width: fit-content;
    cursor: pointer;
}

a#link.nav-link.active {
    color: $secondary;
}
a#link.nav-link.active::after {
    opacity: 1;
    width: 100%;
    left: 0;
}

#link:hover {
    color: $secondary;
}

.navBar {
    background-color: transparent;
    //box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
    transition: all 0.5s ease;
}

.navBar.active {
    transition: all 0.5s ease;
    box-shadow: 0 10px 10px -2px rgba(0, 0, 0, 0.2);
    background-color: $primary;
}

.navBar.active-toggle {
    transition: all 0.5s ease;
    box-shadow: 0 10px 10px -2px rgba(0, 0, 0, 0.2);
    background-color: $primary;
}

.nav-item {
    display: flex;
    justify-content: center;
}

a#link.nav-link::after {
    position: absolute;
    left: 50%;
    bottom: -5px;
    width: 0%;
    height: 2px;
    background: $secondary;
    content: "";
    opacity: 1;
    transition: all 0.3s linear;
}

a#link.nav-link:hover::after {
    opacity: 1;
    width: 100%;
    left: 0;
}

.navbar-toggler:focus,
.navbar-toggler {
    border: none;
    color: transparent;
    box-shadow: none;
    outline: none;
}

.navbar-toggler .collapsed {
    border: 2px;
    outline: none;
    box-shadow: none;
}

.nav.navbar-collapse.collapse {
    float: right;
}


.icon-bar {
    background-color: $secondary;
    width: 30px;
    height: 3px;
    margin-bottom: 5px;
    transition: all 0.2s ease;
}

.icon-bar.active-toggle:nth-of-type(1) {
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    -webkit-transform-origin: 10% 10%;
    -ms-transform-origin: 10% 10%;
    transform-origin: 10% 10%;
}

.icon-bar.active-toggle:nth-of-type(2) {
    opacity: 0;
    filter: alpha(opacity=0);
}

.icon-bar.active-toggle:nth-of-type(3) {
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
    -webkit-transform-origin: 10% 90%;
    -ms-transform-origin: 10% 90%;
    transform-origin: 10% 90%;
}
