@import "../../style/colors.scss";
@import "../../style/metrics.scss";

.project-container {
    display: flex;
    height: 900px;
}

.project-heading {
    color: $title;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
}

.project-card-list-container {
    display: flex;
    width: inherit;
    //height: 500px;
    scroll-behavior: smooth;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-behavior: smooth;
    scrollbar-width: none;
    scroll-snap-type: x mandatory;
    // &.proximity {
    //     scroll-snap-type: x proximity;
    // }
}

.project-card-list-container::-webkit-scrollbar {
    display: none;
}

.project-status{
    position: absolute;
    top: 0;
    right: 0;
    width: 15px;
    height: 15px;
    background-color: yellow;
    border-radius: 100%;
}

.project-card-container {
    display: flex;
    flex-direction: column;
    width: 350px;
    //height: 500px;
    margin-right: $basePadding * 2;
    background-color: $primary-light;
    scroll-snap-align: start;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom: 2px solid $secondary;
    z-index: 1000;
    //border-radius: $baseRadius;
    //background: url("../../assets/Images/project_image/todoappimg.png");
}

.project-img-container {
    //flex: 1;
    width: inherit;
    border-radius: $baseRadius;
    overflow: hidden;
}

.project-img {
    width: inherit;
    height: 280px;
    background-size: cover;
    border-radius: 10px;
    overflow: hidden;
}

.project-info-container {
    flex: 1;
    padding: $basePadding;
    //text-align: center;
    //display: none;
}

.project-info-container:hover {
    display: block;
}

.project-col2 {
    display: flex;
    justify-content: flex-end;
}

.project-title {
    color: $secondary;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
}

.project-name {
    color: $heading;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 23px;
}

.project-desc {
    color: $title;
    text-align: justify;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    height: 50px;
}

#project-card-but {
    position: relative;
    padding: 10px 20px;
    color: $secondary;
    background-color: transparent;
    border: 1px solid $secondary;
    border-radius: $baseRadius;
    box-shadow: none;
}

#project-card-but:hover {
    color: $primary;
    transition: all 0.3s;
}

#project-card-but::after {
    position: absolute;
    left: 0;
    top: 0;
    width: 0%;
    height: 100%;
    background-color: $secondary;
    content: "";
    opacity: 0;
    transition: all 0.3s;
    z-index: -1;
}

#project-card-but:hover::after {
    width: 100%;
    opacity: 1;
    transition: all 0.3s;
}

#project-toggle-but1,
#project-toggle-but2 {
    background-color: transparent;
    border: 1px solid $secondary;
    border-radius: $baseRadius;
    box-shadow: none;
}

#project-toggle-but2 {
    background-color: $secondary;
}

#project-toggle-but2.poject-contron-icon {
    color: $primary;
}

.poject-contron-icon-left {
    padding: 0px;
}

.poject-contron-icon-right {
    color: $primary;
    padding: 0px;
}

@media only screen and (max-width: $mobileScreen) {
    .project-card-container {
        display: flex;
        flex-direction: column;
        width: 350px;
        margin-right: $basePadding * 2;
        background-color: $primary-light;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        border-bottom: 2px solid $secondary;
    }
}

@media only screen and (max-width: $smMobileScreen) {
    .project-card-container {
        display: flex;
        flex-direction: column;
        width: 300px;
        margin-right: $basePadding * 2;
        background-color: $primary-light;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        border-bottom: 2px solid $secondary;
    }
}
